import { Injectable } from "@angular/core";
import { GlobalService } from "./global.service";
import { Observable } from "rxjs/Observable";
import 'rxjs/add/operator/map';
import 'rxjs/observable/throw';
import { Product } from "../models/product";
import { Http, Headers, Response } from "@angular/http";
import { CartService } from "../public/services/cart.service";

@Injectable()
export class StoreService{

    headers:Headers;
    baseurl:String;

    constructor(private _globalservice:GlobalService, private _http:Http){
        this.headers = new Headers({'Content-Type':'application/json'});
        this.baseurl = this._globalservice.apiAddress;
    }


    getproducts():Observable<Product[]> {
        return this._http.get(`${this.baseurl}/store`)
                    .map((response:Response) => response.json())
                    .catch((err:Response) => Observable.throw(err.json()));
    }

    savecart(_cart:CartService):Observable<Response>{
        return this._http.post(`${this.baseurl}/store/cart`,JSON.stringify(_cart)
        ,{headers:this.headers})
        .map((response:Response) => response.json())
        .catch((err:Response) => Observable.throw(err));
    }



}