import { Injectable } from "@angular/core";
import { Http, Headers , Response, RequestOptions} from "@angular/http";
import { AuthenticationService } from "src/app/services/authentication.service";
import { Observable } from "rxjs/Observable";
import 'rxjs/add/operator/map';
import 'rxjs/observable/throw';
import { Product } from "src/app/models/product";
import { GlobalService } from "src/app/services/global.service";


@Injectable()
export class ProductService{

    headers: Headers;
    options: RequestOptions;
    constructor(private _http:Http, private _authservice: AuthenticationService,
        private _globalservice:GlobalService){
        this.headers = new Headers({
            'Content-Type' : 'application/json'
        });

        this.options = new RequestOptions({
            headers: new Headers({
                'authorization' : this._authservice.user.token, 
                'Content-Type' : 'application/json'
            })
        })
    }

    getAllProducts() : Observable<Product[]> {
        return this._http.get(`${this._globalservice.apiAddress}/product`,this.options)
        .map((response:Response) => <Product[]> response.json())
        .catch((error:Response) => Observable.throw(error));
    }

    getProduct(id : String ): Observable<Product> {
        return this._http.get(`${this._globalservice.apiAddress}/product/${id}`, this.options)
        .map((response:Response) => <Product> response.json())
        .catch((error:Response) => Observable.throw(error));
    }

    addProduct(product : Product) : Observable<Response>{
        let id = product._id;
        if(id == undefined){
            return this._http.post(`${this._globalservice.apiAddress}/product`
            ,JSON.stringify(product),this.options)
            .catch((error:Response) => Observable.throw(error));
        }else{
            return this._http.put(`${this._globalservice.apiAddress}/product/${product._id}`,
            JSON.stringify(product), this.options)
            .catch((error:Response) => Observable.throw(error));
        }
    }

    deleteProduct(product : Product) : Observable<Response>{
        let id = product._id;
        if(id != undefined){
            return this._http.delete(`${this._globalservice.apiAddress}/product/${product._id}`,
            this.options)
            .catch((error:Response) => Observable.throw(error));
        }
        
    }

}