import { Injectable } from "@angular/core";
import {Http, Headers, Response, RequestOptions} from '@angular/http';
import { Router } from '@angular/router';
import { Observable } from "rxjs/Observable";
import 'rxjs/add/operator/map';
import 'rxjs/observable/throw';

import { Category } from "src/app/models/category";
import { GlobalService } from "src/app/services/global.service";
import { AuthenticationService } from "src/app/services/authentication.service";



@Injectable()
export class CategoryService{
    headers:Headers;
    options:RequestOptions;
    constructor(private _http:Http, private _router:Router, 
        private _globalservice:GlobalService, private _authservice: AuthenticationService){
        this.headers = new Headers({'Content-Type' : 'application/json'});
        this.options = new RequestOptions(
            {headers:new Headers(
                {'authorization': this._authservice.user.token,'Content-Type' : 'application/json'}
                )}
            )
    }

    getAllcategories(): Observable<Category[]>{
        return this._http.get(this._globalservice.apiAddress + '/category', this.options)
                    .map((categorydata :Response) => <Category[]> categorydata.json())
                    .catch((err:Response) => Observable.throw(err));
    }

    getcategory(_id:String): Observable<Category>{
        return this._http.get(`${this._globalservice.apiAddress}/category/${_id}`, this.options)
                    .map((categorydata) => <Category> categorydata.json())
                    .catch((err:Response) => Observable.throw(err));
    }

    addcategory(category : Category): Observable<Response>{
        console.log(JSON.stringify(category));
        if(category._id == undefined){
            return this._http.post(`${this._globalservice.apiAddress}/category`
            ,JSON.stringify(category), this.options)
            .catch((err:Response) => Observable.throw(err));
        }else{
            return this._http.put(`${this._globalservice.apiAddress}/category/${category._id}`
            ,JSON.stringify(category), this.options)
            .catch((err:Response) => Observable.throw(err));
        }
        
    }

    deletecategory(category:Category) : Observable<Response>{
        if(category._id != undefined){
            return this._http.delete(`${this._globalservice.apiAddress}/category/${category._id}`
            ,this.options)            
            .catch((err:Response) => Observable.throw(err));
        }
    }


}