import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule} from '@angular/router';
import { AdminGraudAuthentication } from './services/admingraudauthentication.service';
import { UserGraudAuthentication } from './services/usergraudauthentication.service';


const approutes: Routes = [
  {path: '', loadChildren: '../app/public/public.module#PublicModule'},
  {path: 'user', loadChildren:'../app/user/user.module#UserModule'
  , canActivate:[UserGraudAuthentication]},
  {path: 'admin', loadChildren:'../app/admin/admin.module#AdminModule'
  , canActivate:[AdminGraudAuthentication]}
];


@NgModule({
  imports: [
    CommonModule, RouterModule.forRoot(approutes)
    
  ],
  declarations: [],
  exports:[RouterModule]
})
export class AppRoutingModule { }

export const AppRoutedComponent = [];
